
import XHeader from 'vux/src/components/x-header/index.vue'

import Scroll from "@/components/scrollAll";
import NoData from "@/components/noData";

export default {
name: "birthdayClient",
components: {
XHeader,
Scroll,
NoData
},
data() {
return {
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
list: [],
page: 1,
count: 20,
canShowNoData: false
}
},
mounted() {
this.getData()
},
methods: {
getData(type) {
this.canShowNoData = false
this.$vux.loading.show({
text: '加载中'
})
axios.get(`/Customer/GetCustomerBirthdayRemind`,{
params: {
days: 7,
page: this.page,
count: this.count
}
}).then(res => {
this.$vux.loading.hide()
this.canShowNoData = true
if (res.data.State === 1) {
if (type) {
if (res.data.Data.List.length > 0) {
this.list.push(...res.data.Data.List);
}
this.$refs.scroll.scroll.finishPullUp();
} else {
this.list = res.data.Data.List;
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate(false);
}
this.$refs.scroll.refresh();
this.$refs.scroll.forceUpdate();
} else {
this.$refs.scroll.forceUpdate();
}
}).catch(e => {
this.$vux.loading.hide()
this.canShowNoData = true
})
}
}
}



